<template>
  <div>
    <div class="table-box">
      <el-table :data="list"  style="width: 100%">
        <el-table-column align="center" prop="uid" width="320" label="用户ID" ></el-table-column>
        <el-table-column align="center" prop="nickname" label="昵称" ></el-table-column>
        <el-table-column align="center"  prop="phone" label="手机号" ></el-table-column>
        <el-table-column align="center"  prop="addtime" label="注册日期" >
          <template slot-scope="scope">
             {{ scope.row.addtime*1 | toTime  }}
          </template>
        </el-table-column>
        <el-table-column align="center"  label="操作" >
          <template slot-scope="scope">
            <el-button  @click="handleClick(scope.row)" type="success" size="mini"  circle> <i class="el-icon-edit"></i></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { getMember,editMember } from "@/request/member"
export default {
  data() {
    return {
       list:[]
    };
  },
  created(){
        this.getlist();  // 获取列表
  },
  methods:{
      async getlist(){
          this.list  = await getMember();
      }
  }
};
</script>
<style scoped>
</style>


